<style>
  .ll-p-m-w {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
  }
  .ll-img-w {
    width: 60px;
    height: 60px;
  }
  .ll-img-w>img {
    width: 60px;
  }
  .ll-p-m-c-r {
    padding: 0 10px;
  }
  .ll-erp-p-no {
    color: #666;
    font-size: 12px;
  }
  .c-g-sfw {
    padding-top: 0 !important;
    padding-bottom: 0 !important;
  }

  .choose-store-table-wrap {
    width: 500px;
    border: 1px solid #ececec;
    border-radius: 4px;
  }
  .choose-goods-wrap {
    border: 1px solid #ececec;
    border-radius: 4px;
  }
</style>

<template>
  <div class="add-form-wraper">
    <el-form ref="form" :model="addParamMsg" label-width="160px">
      <el-form-item label="活动类型" required>
        <p
          style="color: #222;font-size: 16px; font-weight: bold;"
        >{{addParamMsg.activityTypeName}}</p>
      </el-form-item>
      <el-form-item label="选择区域" required>
        <el-input v-model="addParamMsg.regionName" size="small" class="add-input" disabled></el-input>
      </el-form-item>
      <el-form-item label="活动名称" required>
        <el-input v-model="addParamMsg.activityName" size="small" class="add-input" placeholder="请输入活动名称" :disabled="status === 3 || status === 2"></el-input>
        <span>活动名称，限制30个字</span>
      </el-form-item>
      <el-form-item label="开始时间" required>
        <el-date-picker
          class="add-input"
          size="small"
          v-model="addParamMsg.startDatetime"
          type="datetime"
          format="yyyy-MM-dd HH:mm:ss"
          value-format="yyyy-MM-dd HH:mm:ss"
          :picker-options="pickerOptions"
          placeholder="请输入开始时间"
          :disabled="status === 3 || status === 2">
        </el-date-picker>
        <span>活动开始时间</span>
      </el-form-item>
      <el-form-item label="结束时间" required>
        <el-date-picker
          class="add-input"
          size="small"
          v-model="addParamMsg.endDatetime"
          type="datetime"
          format="yyyy-MM-dd HH:mm:ss"
          value-format="yyyy-MM-dd HH:mm:ss"
          :picker-options="pickerOptions"
          placeholder="请输入结束时间"
          :disabled="status === 3 || status === 2">
        </el-date-picker>
        <span>活动结束时间</span>
      </el-form-item>
      <el-form-item label="截单时间" required>
        <el-select class="add-input-inline" v-model="addParamMsg.shortCycleType" placeholder="截单时间" size="small" :disabled="status === 3 || status === 2">
          <el-option
            v-for="item in shortCircleOptions"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
        <el-time-picker
          class="add-input-inline"
          v-model="addParamMsg.cutoffTime"
          placeholder="选择时间"
          format="HH:mm"
          value-format="HH:mm"
          size="small"
          :disabled="status === 3 || status === 2">
        </el-time-picker>
        <span>每天截单即24小时一个周期，每2天截单即48小时一个周期</span>
      </el-form-item>
      <el-form-item label="提货日期" required>
        <el-select class="add-input" v-model="addParamMsg.shortPickUpType" placeholder="提货日期" size="small" :disabled="status === 3 || status === 2">
          <el-option
            v-for="item in shortPickUpTypeOptions"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
        <span>表示一个周期结束后的第二天提货</span>
      </el-form-item>
      <el-form-item label="参与门店" required>
        <el-button size="small" @click="chooseStoreOpen" icon="el-icon-plus" :disabled="status === 3 || status === 2">选择门店</el-button>
        <div v-if="activityStoresCheckedList.length > 0" class="choose-store-table-wrap">
          <el-table
            class="fixed-height"
            :data="activityStoresCheckedList"
            empty-text="暂无选中任何门店"
            max-height="300"
            size="small">
            <el-table-column min-width="220px" label="门店编码/名称">
              <template slot-scope="scope">
                <div>
                  {{scope.row.erpStoreNo}}_{{scope.row.erpStoreName}}
                </div>
              </template>
            </el-table-column>
            <el-table-column min-width="80" prop="cityName" label="城市"></el-table-column>
            <el-table-column min-width="80" prop="saleTypeName" label="门店类型"></el-table-column>
            <el-table-column
              align="center"
              label="操作"
              width="100">
              <template slot-scope="scope" size="small">
                <el-button @click="deleteStoreItem(scope.row)" type="text" size="mini" icon="el-icon-delete" :disabled="status === 3 || status === 2">删除</el-button>
              </template>
            </el-table-column>
          </el-table>
        </div>
      </el-form-item>
      <el-form-item label="活动商品" required>
        <el-button type="primary" @click="goAddGoodsDialog" size="mini" icon="el-icon-plus">添加商品</el-button>
        <el-button @click="showExcelBatchInsertDialog" size="mini" icon="el-icon-plus" v-if="status !== 3 && status !== 2">Excel批量导入</el-button>
        <el-table
          class="fixed-height choose-goods-wrap"
          :data="checkedList"
          empty-text="暂无选中任何商品"
          style="width:100%;"
          max-height="560"
          size="small">
          <el-table-column min-width="220px" label="商品信息">
            <template slot-scope="scope">
              <div class="ll-p-m-w">
                <div class="ll-img-w">
                  <img v-if="scope.row.imgUrl && scope.row.imgUrl != ''" :src="scope.row.imgUrl" alt="" onerror="onerror=null;src='/shop/static/img_error.png'">
                  <img v-else src="/shop/static/img_error.png" alt="">
                </div>
                <div class="ll-p-m-c-r">
                  <div>
                    {{scope.row.goodsName || '-'}}
                  </div>
                  <div class="ll-erp-p-no">SKU编码：{{scope.row.goodsNo || '--'}}</div>
                  <div class="ll-erp-p-no">商品ID：{{scope.row.platformCityGoodsId || '--'}}</div>
                  <div class="ll-erp-p-no">商品编码：{{scope.row.goodsId || '--'}}</div>
                </div>
              </div>
            </template>
          </el-table-column>
          <el-table-column min-width="100px" prop="spec" label="规格"></el-table-column>
          <!-- <el-table-column min-width="130px" label="活动商品名称">
            <template slot-scope="scope">
              <div>
                <el-input v-model="scope.row.activityGoodsName" size="small" placeholder="输入名称" :disabled = "scope.row.disabled"></el-input>
              </div>
            </template>
          </el-table-column> -->
          <el-table-column min-width="130px" label="原价(必填)">
            <template slot-scope="scope">
              <div>
                <el-input v-model="scope.row.price" size="small" placeholder="输入原价" :disabled = "scope.row.disabled"></el-input>
              </div>
            </template>
          </el-table-column>
          <el-table-column min-width="130px" label="活动价(必填)">
            <template slot-scope="scope">
              <div>
                <el-input v-model="scope.row.preSalePrice" size="small" placeholder="输入活动价" :disabled = "scope.row.disabled"></el-input>
              </div>
            </template>
          </el-table-column>
          <el-table-column min-width="130px" label="活动库存(选填)">
            <template slot-scope="scope">
              <div>
                <el-input v-model="scope.row.stock" size="small" placeholder="活动库存" :disabled = "scope.row.disabled"></el-input>
              </div>
            </template>
          </el-table-column>
          <el-table-column min-width="140px" label="每人限购">
            <template slot-scope="scope">
              <div>
                <el-input v-model="scope.row.limitCount" size="small" placeholder="每人限购" :disabled = "scope.row.disabled"></el-input>
              </div>
            </template>
          </el-table-column>
          <el-table-column min-width="140px" :render-header="sortNumHeader">
            <template slot-scope="scope">
              <div>
                <el-input v-model="scope.row.sortNum" size="small" placeholder="排序（0-299越大越靠前）" :disabled = "scope.row.disabled"></el-input>
              </div>
            </template>
          </el-table-column>
          <el-table-column min-width="190px" :render-header="sfInStockHeader">
            <template slot-scope="scope">
              <div>
                <el-radio :disabled="scope.row.disabled" v-model="scope.row.sfInStock" :label="true">是</el-radio>
                <el-radio :disabled="scope.row.disabled" v-model="scope.row.sfInStock" :label="false">否</el-radio>
              </div>
            </template>
          </el-table-column>
          <el-table-column
            fixed="right"
            align="center"
            label="操作"
            width="80">
            <template slot-scope="scope" size="small">
              <el-button @click="deleteItem(scope.row)" type="text" size="mini" icon="el-icon-delete" :disabled = "scope.row.disabled">删除</el-button>
            </template>
          </el-table-column>
        </el-table>
        <p>
          <span style="color:red;">*</span>活动未发布时可以编辑商品，发布后只可添加新商品，不可再编辑已有商品；
        </p>
        <p>
          <span style="color:red;">*</span>活动进行中时新上门店，请在活动商品中发布至门店；
        </p>
      </el-form-item>
      
      <hr class="s-slip-tb">
      <el-form-item>
        <el-button type="primary" size="small" @click="doSaveHandel" :disabled="isDisabled" :icon="isDisabled ? 'el-icon-loading' : null">保存</el-button>
        <el-button size="small" @click="doCancelHandel">取消</el-button>
      </el-form-item>
    </el-form>
    
    <!-- 选择商品 -->
    <choose-goods ref="chooseGoods" @confirmChooseGoods="confirmChooseGoods"></choose-goods>
    <!-- 选择门店 -->
    <choose-store ref="chooseStore" @confirmChooseStore="confirmChooseStore"></choose-store>
    <excel-add ref="excelAdd" @doConfirmHandel="doConfirmHandel"></excel-add>
    <el-dialog 
      title="批量导入失败" 
      :visible.sync="showTipErrorMsg" 
      :close-on-click-modal="false"
      :close-on-press-escape="false"
      width='450px'>
      <div>
        部分商品不符合要求，请按要求修改后再重新导入。<el-button type="text" size="mini" @click="downLoadErrorMsg">下载查看失败原因</el-button>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="showTipErrorMsg = false" size="mini">关 闭</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import Storage from '@/utils/localStorage'
  import * as utils from '@/utils/utils'
  import ChooseStore from "@/components/Activity/Common/ChooseStore"
  import ChooseGoods from "@/components/Activity/Common/ChooseGoods"
  import AddByExcel from "@/components/Common/Excel/AddByExcel"
  import exportSheet from '@/utils/exportExcel'
  const numReg = /^[1-9]*[1-9][0-9]*$/
  const salePriceReg = /((^[1-9]\d*)|^0)(\.\d{0,2}){0,1}$/
  const requestUrl = {
    // 保存活动
    saveActivity: '/plateform/a/preSale/activity/saveActivity',
    // 更新活动
    updateActivity: '/plateform/a/preSale/activity/updateActivity',
    // 详情
    detail: '/plateform/a/preSale/activity/detail',
    // 校验新增活动商品
    checkPreGoodsInsert: '/newretail-shop/a/preSale/goods/checkPreGoodsInsert'
  }
  export default {
    data() {
      return {
        status: 0,
        optType: '', // 此页面操作类型  edit  编辑   copy  复制
        isDisabled: false,
        activityId: '',
        addParamMsg: {
          platformId: 5,
          DIFFCITY: true,
          regionCode: Storage.get('regionCode') || '', // 区域ID
          activityType: 41, // 活动类型
          presaleType: 2, // 预售类型，1-单期，2-周期
          cycleType: 1, // 周期类型，1-短周期，2-长周期
          activityTypeName: '新人专享', // 活动类型名称
          activityName: '', // 活动名称
          startDatetime: '', // 开始时间cycleType
          endDatetime: '', // 结束时间
          shortCycleType: 1, // 周期类型  1 每天 2 每两天
          cutoffTime: '22:00',
          shortPickUpType: 0, // 提货类型 截单后第2天提
          goodsReqList: [],
          storeNoList: []
        },
        shortCircleOptions: [
          {
            label: '每天',
            value: 1
          },
          {
            label: '每2天',
            value: 2
          }
        ],
        shortPickUpTypeOptions: [
          {
            label: '截单后第2天提',
            value: 0
          }
        ],
        pickerOptions: {
          disabledDate(time) {
            return time.getTime() < Date.now() - 8.64e7
          }
        },
        // 选中的商品列表
        checkedList: [],
        // 参与门店选中的
        activityStoresCheckedList: [],
        tempConfig: {
          platformCityGoodsId: '区域商品id(必填)',
          goodsNo: 'SKU编码(选填)',
          skuNo: 'ERP商品编码(选填)',
          goodsName: '活动商品名称(可修改，必填)',
          // activityGoodsName: '活动商品名称(可修改，必填)',
          price: '原价(必填）',
          preSalePrice: '活动价(必填)',
          stock: '活动库存(选填，为空时不限)',
          limitCount: '每人限购(选填，为空时不限)',
          sortNum: '排序(必填)',
          sfInStockStr: '顺丰仓现货（选填，填是/否，空时为是）'
        },
        // 显示下载含有失败原因的excel
        showTipErrorMsg: false, 
        // 后台返回的又失败原因的内容
        hasErrorMsgData: [],
        saveErrorMsgList: [],
        saveErrTempConfig: {
          skuNo: 'ERP商品编码',
          goodsName: '商品名称',
          storeNo: '门店编码',
          storeName: '门店名称',
        }
      }
    },
    beforeRouteEnter(to, from, next) {
      if(to.params.type === 'edit') {
        to.meta.breadcrumbList = [
          {name: '营销活动', path: ''},
          {name: '店铺活动', path: '/store_act'},
          {name: '编辑活动', path: ''}
        ]
      }else if(to.params.type === 'copy') {
        to.meta.breadcrumbList = [
          {name: '营销活动', path: ''},
          {name: '店铺活动', path: '/store_act'},
          {name: '复制活动', path: ''}
        ]
      }
      next()
    },
    created() {
      this.activityId = this.$route.params.activityId
      this.optType = this.$route.params.type
      this.status = this.$route.query.status ? parseInt(this.$route.query.status) : 0
      this.getDetail()
    },
    components: {
      "excel-add": AddByExcel,
      "choose-goods": ChooseGoods,
      "choose-store": ChooseStore
    },
    methods: {
      getDetail() {
        utils.showLoading()
        this.$http.post(requestUrl.detail, {activityId: this.activityId, DIFFCITY: true}).then(res => {
          utils.clearLoading()
          if(res.code === 0) {
            if(res.data) {
              this.addParamMsg.regionCode = res.data.regionCode
              this.addParamMsg.regionName = res.data.regionName
              this.addParamMsg.activityName = res.data.activityName
              if(res.data.activityType) this.addParamMsg.activityType = res.data.activityType || 41
              if(res.data.presaleType) this.addParamMsg.presaleType = res.data.presaleType
              if(res.data.cycleType) this.addParamMsg.cycleType = res.data.cycleType
              this.addParamMsg.startDatetime = res.data.activityStartDate ? res.data.activityStartDate : ''
              this.addParamMsg.endDatetime = res.data.activityEndDate ? res.data.activityEndDate : ""
              this.addParamMsg.shortCycleType = res.data.cutoffDays ? parseInt(res.data.cutoffDays) : 1
              this.addParamMsg.shortPickUpType = 0
              this.addParamMsg.cutoffTime = res.data.cutoffTime
              this.activityStoresCheckedList = res.data.storeNoList && res.data.storeNoList.length ? res.data.storeNoList : []
              this.checkedList = res.data.goodsVoList && res.data.goodsVoList.length > 0 ? res.data.goodsVoList : []
              this.checkedList.forEach(item => {
                item.disabled = this.status === 2 || this.status === 3
                item.sfInStock = item.sfInStock
              })
            }
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          utils.clearLoading()
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },
      /**
       * [doSaveHandel 保存]
       * @return {[type]} [description]
       */
      doSaveHandel() {
        if(!this.addParamMsg.activityName) {
          this.$message("请输入活动名称")
          return
        }else {
          if(this.addParamMsg.activityName.length > 30) {
            this.$message("活动名称,限制30个字")
            return
          }
        }
        // 起止日期
        if(!this.addParamMsg.startDatetime) {
          this.$message("请选择活动开始时间")
          return
        }
        if(!this.addParamMsg.endDatetime) {
          this.$message("请选择活动结束时间")
          return
        }
        let startDatetimeNum = Date.parse(new Date(this.addParamMsg.startDatetime))
        let endDatetimeNum = Date.parse(new Date(this.addParamMsg.endDatetime))
        if(endDatetimeNum <= startDatetimeNum) {
          this.$message("活动结束时间不能早于或等于活动开始时间")
          return
        }
        if(!this.addParamMsg.cutoffTime) {
          this.$message("请选择截单时间")
          return
        }
        let storeNoList = this.activityStoresCheckedList.map(item => {
          return item.erpStoreNo
        })
        this.addParamMsg.storeNoList = storeNoList
        if(this.addParamMsg.storeNoList.length === 0) {
          this.$message("请选择参与门店")
          return
        }
        let goodsReqListArr = []
        for(let item of this.checkedList) {
          if(!item.goodsName) {
            this.$message(`请输入(${item.goodsName})的商品名称`)
            return
          }
          // if(!item.activityGoodsName) {
          //   this.$message(`请输入(${item.goodsName})的活动商品名称`)
          //   return
          // }
          if(!item.price) {
            this.$message(`请输入(${item.goodsName})的原价`)
            return
          }else {
            if(!salePriceReg.test(item.price) || item.price == 0) {
              this.$message('原价是大于0，小数保留小数点后2位') 
              return
            }
          }
          if(!item.preSalePrice) {
            this.$message(`请输入(${item.goodsName})的活动价`)
            return
          }else {
            if(!salePriceReg.test(item.preSalePrice) || item.preSalePrice == 0) {
              this.$message('活动价是大于0，小数保留小数点后2位') 
              return
            }
          }

          if(parseFloat(item.preSalePrice) > parseFloat(item.price)) { 
            this.$message(`(${item.goodsName})的原价必须大于等于活动价`)
            return
          }
          
          if(item.stock != 0 && item.stock) {
            if(numReg.test(item.stock)) {
              if(item.stock < 0) {
                this.$message("门店限售库存不能小于0")
                return
              }
            }else {
              this.$message("门店限售库存格式不正确")
              return
            }
          }

          if(item.limitCount != 0 && item.limitCount) {
            if(numReg.test(item.limitCount)) {
              if(item.limitCount < 0) {
                this.$message("限购数量不能小于0")
                return
              }
            }else {
              this.$message("输入限购数量格式不正确")
              return
            }
          }

          if(item.sortNum != 0 && item.sortNum) {
            if(numReg.test(item.sortNum)) {
              if(item.sortNum < 0 || item.sortNum > 299) {
                this.$message("排序权重为0 — 299的整数，越大越靠前")
                return
              }
            }else {
              this.$message("排序权重为0 — 299的整数，越大越靠前")
              return
            }
          }

          let goodsReqListItem = {
            goodsId: item.goodsId,
            goodsNo: item.goodsNo,
            skuNo: item.skuNo,
            goodsName: item.goodsName,
            // activityGoodsName: item.activityGoodsName,
            preSalePrice: item.preSalePrice,
            price: item.price,
            stock: item.stock,
            limitCount: item.limitCount,
            sortNum: item.sortNum,
            spec: item.spec,
            platformCityGoodsId: item.platformCityGoodsId,
            sfInStock: item.sfInStock
          }
          goodsReqListArr.push(goodsReqListItem)
        }
        if(goodsReqListArr.length === 0) {
          this.$message("请先设置活动商品")
          return
        }
        let params = {
          DIFFCITY: this.addParamMsg.DIFFCITY,
          regionCode: this.addParamMsg.regionCode,
          activityName: this.addParamMsg.activityName,
          presaleType: this.addParamMsg.presaleType,
          startDatetime: this.addParamMsg.startDatetime,
          endDatetime: this.addParamMsg.endDatetime,
          storeNoList: this.addParamMsg.storeNoList,
          activityType: this.addParamMsg.activityType,
          cycleType: this.addParamMsg.cycleType,
          cutoffType: 0,
          cutoffDays: this.addParamMsg.shortCycleType,
          cutoffTime: this.addParamMsg.cutoffTime,
          pickUpType: this.addParamMsg.shortPickUpType,
          goodsReqList: goodsReqListArr
        }
        // 请求接口  区分更新还是复制
        let saveOrUpdateUrl = requestUrl.updateActivity 
        if(this.optType === 'edit') {
          // 编辑
          params.activityId = this.activityId
          saveOrUpdateUrl = requestUrl.updateActivity 
        }else if(this.optType === 'copy') {
          // 复制
          saveOrUpdateUrl = requestUrl.saveActivity 
        }
        this.isDisabled = true
        this.$http.post(saveOrUpdateUrl, {...params}).then(res => {
          // 保存按钮可再次点击
          setTimeout(() => {
            this.isDisabled = false
          }, 800)
          if(res.code === 0) {
            if(res.data && typeof(res.data) === 'object' && res.data.length > 0) {
              this.$alert("<div><p>活动保存成功</p><p style='color: red;'>有商品在门店线上不可售</p></div>", '温馨提示', {
                dangerouslyUseHTMLString: true,
                showCancelButton: true,
                cancelButtonText: "下载明细",
                showConfirmButton: true,
                confirmButtonText: '关闭'
              }).then(() => {}).catch(() => {
                this.saveErrorMsgList = res.data
                this.downLoadSaveErrorMsg()
              });
            }else {
              this.$message.success("保存成功")
              this.$router.push('/store_act')
            }
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.isDisabled = false
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },
      /**
       * [doCancelHandel 取消]
       * @return {[type]} [description]
       */
      doCancelHandel() {
        this.$confirm('确定取消吗?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$router.push('/store_act')
        }).catch(() => {})
      },
      /**
       * [goAddGoodsDialog 添加商品]
       * @return {[type]} [description]
       */
      goAddGoodsDialog() {
        if(this.$refs.chooseGoods) {
          let _params = {
            title: '选择商品',
            regionCode: this.addParamMsg.regionCode,
            checkedList: this.checkedList
          }
          this.$refs.chooseGoods.setDefault(_params)
        }
      },
      /**
       * [confirmChooseGoods 选中商品并关闭]
       * @param  {[type]} val [description]
       * @return {[type]}      [description]
       */
      confirmChooseGoods(val) {
        this.checkedList = val.checkedList || []
      },
      /**
       * [deleteItem 删除]
       * @param  {[type]} _row [description]
       * @return {[type]}      [description]
       */
      deleteItem(_row) {
        let platformCityGoodsId = _row.platformCityGoodsId
        let resCheckedListArr = this.checkedList.filter(item => {
          return item.platformCityGoodsId != platformCityGoodsId
        })

        this.checkedList = JSON.parse(JSON.stringify(resCheckedListArr))
      },
      sortNumHeader() {
        return (
          <el-popover
            placement="top-start"
            trigger="hover"
            content="0-299 越大越靠前">
            <span slot="reference">排序权重<i class="el-icon-question"></i></span>
          </el-popover>
        )
      },
      /**
       * [chooseStoreOpen 选择门店]
       * @return {[type]} [description]
       */
       chooseStoreOpen() {
        let params = {
          platformId: this.addParamMsg.platformId,
          regionCode: this.addParamMsg.regionCode,
          title: '选择门店',
          checkedList: this.activityStoresCheckedList
        }
        this.$refs.chooseStore.setDefault(params)
      },
      /**
       * [deleteItem 删除选中的门店]
       * @param  {[type]} _row [description]
       * @return {[type]}      [description]
       */
      deleteStoreItem(_row) {
        let erpStoreNo = _row.erpStoreNo
        this.activityStoresCheckedList = this.activityStoresCheckedList.filter(item => {
          return item.erpStoreNo !== erpStoreNo
        })
      },
      /**
       * [confirmChooseStore 选中的门店]
       * @param  {[type]} value [description]
       * @return {[type]}        [description]
       */
      confirmChooseStore(value) {
        let activityStoresCheckedList = []
        if(value.checkedList && value.checkedList.length) {
          activityStoresCheckedList = value.checkedList
        }else {
          activityStoresCheckedList = []
        }
        this.activityStoresCheckedList = activityStoresCheckedList
      },
      sfInStockHeader() {
        return (
          <el-popover
            placement="top-start"
            width="260"
            trigger="hover">
            <div>
              <p>1、顺丰标品设为顺丰仓现货时，下单会校验仓库库存；</p>
              <p>2、本来鲜生鲜品无需设置；</p>
            </div>
            <span slot="reference">是否为顺丰仓现货<i class="el-icon-question"></i></span>
          </el-popover>
        )
      },
      /**
       * @description: 导入excel后确认框
       * @param {type} 
       * @return: 
       */
      doConfirmHandel(value) {
        let excelDataList = value.dataList || []
        excelDataList.forEach(item => {
          item.activityType = 41
        })
        this.$http.post(requestUrl.checkPreGoodsInsert, [...excelDataList]).then(res => {
          this.$refs.excelAdd.closeDisabled()
          this.$refs.excelAdd.closeDialog()
          if(res.code === 0) {
            this.hasErrorMsgData = res.data && res.data.length ? res.data : []
            let isOk = this.hasErrorMsgData.some(item => {
              return item.msgType === 1
            })
            if(isOk) {
              this.showTipErrorMsg = true
            }else {
              let checkedListTemp = [...this.checkedList]
              checkedListTemp.forEach(i => {
                this.hasErrorMsgData.forEach(j => {
                  if(i.goodsId === j.goodsId && i.goodsNo === j.goodsNo && i.platformCityGoodsId === j.platformCityGoodsId && i.skuNo === j.skuNo) {
                    i.goodsName = j.goodsName
                    // i.activityGoodsName = j.activityGoodsName
                    i.price = j.price
                    i.preSalePrice = j.preSalePrice
                    i.stock = j.stock
                    i.limitCount = j.limitCount
                    i.sortNum = j.sortNum
                    i.sfInStock = j.sfInStock
                  }
                })
              })
              let hash = {}
              this.checkedList = [...checkedListTemp, ...this.hasErrorMsgData].reduceRight((item, next) => {
                hash[next.platformCityGoodsId] ? '' : hash[next.platformCityGoodsId] = true && item.push(next)
                return item
              }, [])
            }
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.$refs.excelAdd.closeDisabled()
          this.$refs.excelAdd.closeDialog()
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })  
      },
      showExcelBatchInsertDialog() {
        if(this.$refs.excelAdd) {
          let val = {
            title: 'Excel 批量导入',
            tempConfig: this.tempConfig
          }
          this.$refs.excelAdd.setDefaultMsg(val)
        }
      },
      downLoadErrorMsg() {
        let downloadExcelConfig = {...this.tempConfig}
        downloadExcelConfig.msgTypeStr = '验证结果'
        downloadExcelConfig.msg = '失败原因'
        this.hasErrorMsgData.forEach(item => {
          item.msgTypeStr = item.msgType === 1 ? '验证失败' : '验证成功'
        })
        const tHeader = utils.objectToArray(downloadExcelConfig, false)
        const filterVal = utils.objectToArray(downloadExcelConfig, true)
        exportSheet.ExportJsonToExcel({
          header: tHeader,
          filterVal,
          data: this.hasErrorMsgData,
          filename: `Excel批量导入模板`
        })
        setTimeout(() => {
          this.$notify.success({
            title: '温馨提示',
            message: `Excel批量导入模板下载成功`
          })
          this.isDisabled = false
        }, 500)
      },
      downLoadSaveErrorMsg() {
        let downloadExcelConfig = {...this.saveErrTempConfig}
        const tHeader = utils.objectToArray(downloadExcelConfig, false)
        const filterVal = utils.objectToArray(downloadExcelConfig, true)
        exportSheet.ExportJsonToExcel({
          header: tHeader,
          filterVal,
          data: this.saveErrorMsgList,
          filename: `活动保存失败商品明细`
        })
        setTimeout(() => {
          this.$notify.success({
            title: '温馨提示',
            message: `活动保存失败商品明细下载成功`
          })
        }, 500)
      }
    },
    beforeRouteLeave (to, from, next){
      to.meta.keepAlive = true;
      next();
    }
  }
</script>


